.banner {
    color: #fff;
    object-fit: contain;
    height: 548px;
    position: relative;
}

.banner__contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: .3rem;
}

.banner__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: .2vw;
    margin-right: 1rem;
    background-color: rgba(51,51,51,.5);
    padding: .5rem 2rem;
}
.banner__button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all .2s;
}

.banner__description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: .8rem;
    max-width: 360px;
    height: 80px;
}

.banner--fadeBottom {
    height: 7.4rem;
    position: absolute;
    bottom: 0;
    width: 100%;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}